@font-face {
    font-family: 'Source Sans 3';
    src: local('Source Sans 3 Regular'),local('SourceSans3-Regular'),url(/fonts/SourceSans3-Regular.woff2) format('woff2');
    font-weight: 400;
    font-style: normal;
    font-display: swap
}

@font-face {
    font-family: 'Source Sans 3';
    src: local('Source Sans 3 SemiBold'),local('SourceSans3-SemiBold'),url(/fonts/SourceSans3-SemiBold.woff2) format('woff2');
    font-weight: 600;
    font-style: normal;
    font-display: swap
}

@font-face {
    font-family: 'Source Sans 3';
    src: local('Source Sans 3 Bold'),local('SourceSans3-Bold'),url(/fonts/SourceSans3-Bold.woff2) format('woff2');
    font-weight: 700;
    font-style: normal;
    font-display: swap
}

html {
  --color-dark: #08112a;
  --color-dark-10: #111827;
  --color-dark-20: #2e3238;
  --color-dark-50: #5a6581;
  --color-dark-80: #818daf;
  --color-dark-140: #d6dce9;
  --color-dark-150: #e7eaf2;
  --color-dark-166: #f6f7fb;
  --color-light: #fff;
  --color-gray-10: #192538;
  --color-gray-50: #52586a;
  --color-gray-80: #8a8a8a;
  --color-gray-100: #b6becc;
  --color-gray-150: #ededee;
  --color-gray-155: #eaeaeb;
  --color-gray-160: #fafafc;
  --color-primary: #4050ff;
  --color-primary-50: #7782fc;
  --color-primary-70: #9ca4ff;
  --color-primary-100: #caceff;
  --color-gold: #e48c23;
  --color-gold-10: #983705;
  --color-gold-30: #3b2c26;
  --color-gold-40: #f2c94c;
  --color-gold-50: #ffdb5b;
  --color-gold-110: #f8e5b9;
  --color-red: #d0011b;
  --color-green: green;
  --color-green-30: #30ab66;
  --ff1: Montserrat, Corbel, "URW Gothic", source-sans-pro, sans-serif;
  --ff2: "Source Sans 3", "Gill Sans Nova", Ubuntu, Calibri, "DejaVu Sans", source-sans-pro, sans-serif;
  --ff3: "PT Serif", Charter, "Bitstream Charter", "Sitka Text", Cambria, serif;
  --fz1: clamp(2.125rem,0.964rem + 3.095vw,3.75rem);
  --lh1: 1.16667;
  --fz2: clamp(1.938rem,1.089rem + 2.262vw,3.125rem);
  --lh2: 1.2;
  --fz3: clamp(1.75rem,1.214rem + 1.429vw,2.5rem);
  --lh3: 1.25;
  --fz4: clamp(1.563rem,1.116rem + 1.19vw,2.188rem);
  --lh4: 1.28571;
  --fz5: clamp(1.313rem,0.911rem + 1.071vw,1.875rem);
  --lh5: 1.33333;
  --fz6: clamp(1.188rem,0.875rem + 0.833vw,1.625rem);
  --lh6: 1.38462;
  --fz7: clamp(1.125rem,0.902rem + 0.595vw,1.438rem);
  --lh7: 1.3913;
  --fz8: clamp(1.063rem,0.929rem + 0.357vw,1.25rem);
  --lh8: 1.5;
  --fz9: clamp(1rem,0.911rem + 0.238vw,1.125rem);
  --lh9: 1.33333;
  --fz10: 1rem;
  --lh10: 1.5;
  --fz11: .875rem;
  --lh11: 1.42857;
  --fz12: .75rem;
  --lh12: 1.5;
  --gap0: clamp(3.125rem,0.893rem + 5.952vw,6.25rem);
  --gap1: clamp(2.5rem,0.714rem + 4.762vw,5rem);
  --gap2: clamp(1.875rem,0.536rem + 3.571vw,3.75rem);
  --gap3: clamp(1.25rem,0.357rem + 2.381vw,2.5rem);
  --gap4: clamp(0.938rem,0.268rem + 1.786vw,1.875rem);
  --gap5: clamp(0.75rem,0.214rem + 1.429vw,1.5rem);
  --gap6: clamp(0.5rem,0.143rem + 0.952vw,1rem);
  --gap7: clamp(0.375rem,0.107rem + 0.714vw,0.75rem);
  --gap8: clamp(0.25rem,0.071rem + 0.476vw,0.5rem);
  --gap9: .25rem;
}

body {
  font-family: var(--ff2);
  margin: 0;
  background-color: #fff;
}
